/*
* CSS Reset and basic styles
* Most of the fixes are from Bootstrap [_reboot.scss]:
  https://github.com/twbs/bootstrap/blob/main/scss/_reboot.scss

* and thanks to Josh's Custom CSS Reset:
  https://www.joshwcomeau.com/css/custom-css-reset/
*/
*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

@media screen and (max-width: 575.98px) {
  :root {
    font-size: var(--flatify__min-font-size-with-unit);
  }
}
@media (min-width: 576px) and (max-width: 1200px) {
  :root {
    font-size: calc( var(--flatify__min-font-size-with-unit) + ( var(--flatify__max-font-size) - var(--flatify__min-font-size) ) * ( ( 100vw - 576px ) / ( 1200 - 576 ) ) );
  }
}
@media screen and (min-width: 1200px) {
  :root {
    font-size: var(--flatify__max-font-size-with-unit);
  }
}
@media (prefers-reduced-motion: no-preference) {
  :root {
    scroll-behavior: smooth;
  }
}

body {
  margin: 0;
  font-family: var(--flatify__primary-font-family);
  line-height: var(--flatify__base-line-height);
  text-align: var(--flatify__body-text-align);
  color: var(--flatify__txt-color);
  background-color: var(--flatify__bg-color);
  -webkit-tap-highlight-color: var(--flatify__tap-highlight-color);
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

h6, h5, h4, h3, h2, h1 {
  margin-top: 0;
  margin-bottom: var(--flatify__heading-mb);
  font-family: var(--flatify__heading-font-family, --flatify__primary-font-family);
  font-style: var(--flatify__heading-font-style);
  font-weight: var(--flatify__heading-font-weight);
  color: var(--flatify__heading-color);
}
h6 strong, h5 strong, h4 strong, h3 strong, h2 strong, h1 strong,
h6 b,
h5 b,
h4 b,
h3 b,
h2 b,
h1 b {
  font-weight: var(--flatify__extra-bold-font-weight);
}

h1 {
  font-size: 3.5em;
  line-height: var(--flatify__small-line-height);
}

h2 {
  font-size: 3em;
  line-height: var(--flatify__small-line-height);
}

h3 {
  font-size: 2.35em;
  line-height: var(--flatify__small-line-height);
}

h4 {
  font-size: 2em;
  line-height: var(--flatify__base-line-height);
}

h5 {
  font-size: 1.65em;
  line-height: var(--flatify__base-line-height);
}

h6 {
  font-size: 1.35em;
  line-height: var(--flatify__base-line-height);
}

p {
  line-height: var(--flatify__large-line-height);
  margin-top: 0;
  margin-bottom: var(--flatify__paragraph-mb);
}
p:last-of-type {
  margin-bottom: 0;
}

abbr[title] {
  cursor: help;
  text-decoration: var(--flatify__txt-color-light) underline dotted 0.125em;
  text-decoration-skip-ink: none;
}

address {
  margin-bottom: 1em;
  font-style: normal;
  line-height: inherit;
}

ol,
ul {
  padding-left: 2em;
}

ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 1em;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

dt {
  font-weight: var(--flatify__medium-font-weight);
}

dd {
  margin-bottom: 0.5em;
  margin-left: 0;
}

b,
strong {
  font-weight: var(--flatify__bold-font-weight);
}

mark {
  color: var(--flatify__txt-color-dark);
}
mark:not(.inline) {
  border-radius: calc(var(--flatify__border-radius) * 0.38);
  padding: 0 0.32em;
  background: var(--flatify__color-yellow-light);
}
mark.inline {
  --flatify__local-bg-color: var(--flatify__color-yellow-light);
  position: relative;
  white-space: nowrap;
  margin: 0 0.32em;
  background: transparent !important;
}
mark.inline:before {
  content: " ";
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background: var(--flatify__local-bg-color);
  border-radius: calc(var(--flatify__border-radius) * 0.38);
  opacity: 0.9;
  transform: rotate(-3deg) scale(1.1, 1.2) skew(-10deg);
}

sub,
sup {
  position: relative;
  font-size: 0.75em;
  line-height: 0;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

a:not(.button) {
  color: var(--flatify__link-color);
  text-decoration: var(--flatify__link-decoration);
  transition: color var(--flatify__simple-transition), background var(--flatify__simple-transition);
}
a:not(.button):hover {
  color: var(--flatify__link-color__hover);
  text-decoration: var(--flatify__link-decoration__hover);
}
a:not(.button):focus {
  color: var(--flatify__link-color__focus);
  text-decoration: var(--flatify__link-decoration__focus);
}

pre,
code,
kbd,
samp {
  direction: ltr;
  unicode-bidi: bidi-override;
  font-size: 1em;
  font-family: var(--flatify__code-font-family);
}

pre {
  overflow: auto;
  display: block;
  padding: 1em;
  margin-top: 0;
  margin-bottom: 1em;
  background: var(--flatify__bg-color-dark);
  border: var(--flatify__border-width) solid var(--flatify__bg-color-darker);
  border-radius: var(--flatify__border-radius);
}
pre code {
  color: inherit;
  word-break: normal;
}

code {
  vertical-align: middle;
  font-size: 0.85em;
  word-wrap: break-word;
  padding: 0.18em 0.32em;
  background: var(--flatify__bg-color-dark);
  border: var(--flatify__border-width) solid var(--flatify__bg-color-darker);
  border-radius: calc(var(--flatify__border-radius) * 0.5);
}
a > code {
  color: inherit;
}

a:focus-visible,
button:focus-visible {
  outline: 0.18em solid currentColor;
}

img,
svg {
  vertical-align: middle;
}

img,
picture,
video,
canvas {
  display: block;
  max-width: 100%;
}

label {
  display: inline-block;
}

button:focus:not(:focus-visible) {
  outline: 0;
}

input,
button,
textarea,
select {
  font: inherit;
}

input,
button,
select,
optgroup,
textarea {
  margin: 0;
  font-family: inherit;
  line-height: inherit;
}

button,
select {
  text-transform: none;
}

[role=button] {
  cursor: pointer;
}

select {
  word-wrap: normal;
}
select:disabled {
  opacity: 1;
}

[list]::-webkit-calendar-picker-indicator {
  display: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}
button:not(:disabled),
[type=button]:not(:disabled),
[type=reset]:not(:disabled),
[type=submit]:not(:disabled) {
  cursor: pointer;
}

::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

textarea {
  resize: vertical;
}

fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

legend {
  float: left;
  width: 100%;
  padding: 0;
  margin-bottom: 0.5em;
  font-weight: var(--flatify__bold-font-weight);
  line-height: inherit;
}
legend + * {
  clear: left;
}

::-webkit-datetime-edit-fields-wrapper,
::-webkit-datetime-edit-text,
::-webkit-datetime-edit-minute,
::-webkit-datetime-edit-hour-field,
::-webkit-datetime-edit-day-field,
::-webkit-datetime-edit-month-field,
::-webkit-datetime-edit-year-field {
  padding: 0;
}

::-webkit-inner-spin-button {
  height: auto;
}

[type=search] {
  outline-offset: -2px;
  -webkit-appearance: textfield;
}

[type=tel],
[type=url],
[type=email],
[type=number] {
  direction: ltr;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-color-swatch-wrapper {
  padding: 0;
}

::file-selector-button {
  font: inherit;
}

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: button;
}

output {
  display: inline-block;
}

iframe {
  border: 0;
}

summary {
  display: list-item;
  cursor: pointer;
}

progress {
  vertical-align: baseline;
}

[hidden] {
  display: none !important;
}

:root {
  --flatify__blockquote-accent-color: var(--flatify__color-accent-primary);
}

blockquote {
  position: relative;
  z-index: 0;
  overflow: hidden;
  line-height: var(--flatify__large-line-height);
  margin: 0 0 var(--flatify__paragraph-mb);
  border-radius: calc(var(--flatify__border-radius) * 0.5);
  border-style: solid;
  border-color: var(--flatify__blockquote-accent-color);
  border-width: 0 0 0 0.5em;
}
blockquote:not(.no-icon):before {
  content: " ";
  position: absolute;
  z-index: -1;
  opacity: 0.1;
  background-color: currentColor;
  mask: var(--flatify__blockquote-icon) no-repeat 50% 50%;
  -webkit-mask: var(--flatify__blockquote-icon) no-repeat 50% 50%;
  filter: brightness(100%);
}
blockquote:not(.large) {
  font-size: 1em;
  line-height: var(--flatify__large-line-height);
  padding: 0.75em 1.25em;
  border-radius: calc(var(--flatify__border-radius) * 0.5) var(--flatify__border-radius) var(--flatify__border-radius) calc(var(--flatify__border-radius) * 0.5);
}
blockquote:not(.large):before {
  top: 1em;
  width: 7em;
  height: 7em;
  left: 1em;
}
blockquote.large {
  font-size: 1.25em;
  border-width: 0.5em 0;
  padding: 1.25em 0.75em;
  margin-top: 2em;
}
blockquote.large:before {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 15em;
  height: 15em;
  margin: auto;
}
blockquote.large:not(.text-align-fill) {
  text-align: center;
}
blockquote cite {
  display: block;
  font-size: 0.64em;
  font-style: normal;
  letter-spacing: 0.18em;
  text-transform: uppercase;
  padding-top: 1.5em;
  opacity: 0.8;
}

figure {
  position: relative;
  width: max-content;
  max-width: 100%;
  height: max-content;
  margin: 0 0 1em;
  border-radius: var(--flatify__border-radius);
}
figure img {
  width: 100%;
  height: 100%;
  border-radius: var(--flatify__border-radius);
}
figure figcaption {
  font-size: 0.9em;
  padding: 1em 0;
}
figure .source {
  --flatify__local-border-color: var(--flatify__txt-color-light);
  position: absolute;
  top: 1em;
  font-size: 0.75em;
  line-height: 1;
  padding: 0.5em;
  color: var(--flatify__txt-color-inverted);
  background: var(--flatify__txt-color-dark);
  border: var(--flatify__border-width) solid var(--flatify__local-border-color);
  border-radius: calc(var(--flatify__border-radius) * 0.75);
  right: 1em;
}
figure.fill {
  width: 100% !important;
}
figure.centered {
  margin-left: auto;
  margin-right: auto;
}
figure.overlay-caption {
  overflow: hidden;
}
figure.overlay-caption figcaption {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding-left: 1em;
  padding-right: 1em;
  color: var(--flatify__txt-color-inverted);
  background: linear-gradient(0, var(--flatify__txt-color-dark), transparent);
}

